*{
	box-sizing: border-box;
}

.Calculator_Wrapper{
	width: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
}

.Calculator {
	width: 800px;
	border: 1px solid silver;
	border-radius: 10px;
	padding: 16px;
}

@media(max-width: 810px) {
	.Calculator {
		width: 96%;
	}
}